<template>
  <div>
    <h3>Change Password</h3>
    <b-alert variant="success" show class="mt-4">Change your password.</b-alert>
    <b-row class="mt-3">
      <b-col md="6" offset="3">
        <b-form>
          <b-form-input
            v-model="passwordForm.password"
            type="password"
            placeholder="New Password"
            required
          ></b-form-input>
          <b-form-input
            v-model="passwordForm.confirmPassword"
            type="password"
            placeholder="Confirm Password"
            class="mt-4"
            required
          ></b-form-input>
          <b-button
            block
            size="lg" 
            variant="primary" 
            class="mt-4"
            @click="handleChangePassword">Continue
          </b-button>
        </b-form>
      </b-col>
    </b-row>
  </div>
</template>
<script>
export default {
  name: 'LoginView',
  data() {
    return {
      passwordForm: {
        password: '',
        confirmPassword: ''
      }
    }
  },
  methods: {
    handleChangePassword() {
      this.$store.dispatch('auth/changePassword', this.passwordForm).then(() => {
        this.$toasted.success('Change Password Success!')
        this.passwordForm = {
          password: '',
          confirmPassword: ''
        }
      }).catch(() => {

      })
    }
  } 
}
</script>